.comics__container {
    display: flex;
    flex-wrap: wrap;   
    margin: auto;
    max-width: 1200px;
}

.comics__item {
    width: 250px;
    margin: var(--spacing-large);
    list-style: none;
    background-color: var(--color-light);
    transition: 0.5s;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    cursor: pointer;
}

.comics_item:hover {
    transform: rotate(-30deg) scale(0.95);
    filter: saturate(250%);
    background-color: aqua;
}

.comics__name {
    display: flex;
    margin: auto;
    padding: var(--spacing-small) var(--spacing-medium);
}

.comics__img {
    width: 250px;
    display: flex;
    justify-content: start;
}